.wrapper {
  position: relative;
  display: flex;
  flex-flow: column;
}

.container {
  position: relative;
  display: flex;
  width: auto;
  flex-flow: row wrap;
}

.video {
  display: flex;
  flex-flow: column-reverse nowrap;
  align-items: center;
  justify-content: center;
  background-color: rgba(128, 128, 128, 0.05);
  margin: 1rem;
  padding: 1rem;
  border-radius: 5px;
}

.video__name {
  color: gray;
}